import React, { useEffect, useState } from 'react';
import GoogleMapsReact from 'google-map-react';

import Marker from './Marker';

import '../css/map.css';

const backendUrl = process.env.GATSBY_BE_URL;
const mapsKey = process.env.GATSBY_MAPS_KEY;

const potholeQuery = `query potholeQuery {
        potholes {
            name
            lat
            lng
        }
    }
    `;
const Map = function () {
  const [potholes, setPotholes] = useState([]);

  const defaultProps = {

    center: {
      lat: 33.781727,
      lng: -84.370456,
    },
    zoom: 14,
  };

  useEffect(() => fetch(backendUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
    body: JSON.stringify({
      query: potholeQuery,
    }),
  })
    .then((r) => r.json())
    .then((data) => {
      setPotholes(data.data.potholes);
    }), []);

  return (
    <div className="map">
      <GoogleMapsReact
        bootstrapURLKeys={{ key: mapsKey }}
        defaultCenter={defaultProps.center}
        defaultZoom={defaultProps.zoom}
      >
        {potholes.map((pothole) => (
          <Marker
            key={pothole.name}
            text="POTHOLE"
            lat={pothole.lat}
            lng={pothole.lng}
          />
        ))}
      </GoogleMapsReact>
    </div>
  );
};

export default Map;
